import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    count: 0,
    isShowLogin:false
}

const mutations = {
  mutationsSetIsShowLogin(state, isShow = false){
    return (state.isShowLogin = isShow)
  }
}

export default new Vuex.Store({
    state, mutations
})


