<template>

  <div class="hello"
       id="app">

    <login v-on:sendLoginPageInfor="receiveLoginPageInfor"></login>

    <el-menu :default-active="activeIndex2"
             class="el-menu-demo"
             mode="horizontal"
             @select="handleSelect"
             background-color="#fff"
             text-color="#000"
             active-text-color="red">
      <el-menu-item index="1">主页</el-menu-item>
      <el-submenu index="2">
        <template slot="title">我的工作台</template>
        <el-menu-item index="2-1">选项1</el-menu-item>
        <el-menu-item index="2-2">选项2</el-menu-item>
        <el-menu-item index="2-3">选项3</el-menu-item>
        <el-submenu index="2-4">
          <template slot="title">选项4</template>
          <el-menu-item index="2-4-1">选项1</el-menu-item>
          <el-menu-item index="2-4-2">选项2</el-menu-item>
          <el-menu-item index="2-4-3">选项3</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="3"
                    disabled>消息中心</el-menu-item>
      <el-menu-item index="4">
        <el-avatar> user </el-avatar>
      </el-menu-item>
      <el-menu-item index="5"
                    style="float:right;"
                    @click="showLoginPage">
        <span>{{signIn}}</span>
        <el-dropdown v-show="username != ''">
          <span class="el-dropdown-link">
            <el-avatar>{{username}}</el-avatar>
          </span>

          <el-dropdown-menu slot="dropdown">
            <transition name="fade"
                        enter-active-class="animated lightSpeedIn"
                        leave-active-class="animated lightSpeedOut">
              <div>
                <el-avatar style="float:left;">{{username}}</el-avatar> &nbsp;<span style="height:40px;line-height:40px;text-align:center;">{{username}}</span>
                <el-card>
                  <div>
                    <span>Recent Login In Time : </span>
                    <div class="bottom clearfix">
                      <time class="time">2019-12-22</time>
                    </div>

                    <el-row :gutter="20">
                      <el-col :span="16"><a @click="perfectInfor">完善信息</a></el-col>
                      <el-col :span="8"><a @click="exit">退出</a></el-col>
                    </el-row>

                  </div>
                </el-card>
              </div>
            </transition>
          </el-dropdown-menu>
        </el-dropdown>

      </el-menu-item>
    </el-menu>
    <el-container>
      <el-aside width="10%"
                style="height:600px;">

        <!-- <happy-scroll color="rgba(0,0,0,0.5)" size="10" resize> -->
        <el-collapse style="width:100%"
                     accordion>

          <el-collapse-item v-for="(item, index) in worldList"
                            :key="index">
            <template slot="title">
              {{item.name}} <i class="header-icon el-icon-info"></i>
            </template>
            <div>
              <el-row>
                <el-col :span="12">
                  <el-button type="primary"
                             icon="el-icon-iconfont-mini-icon-emotion"
                             size="mini"
                             @click="addFriend">添加</el-button>
                </el-col>
                <el-col :span="12">
                  <el-button type="primary"
                             icon="el-icon-iconfont-mini-icon-qq"
                             size="mini"
                             @click="weChat(item.id, item.name)">聊天</el-button>
                </el-col>
              </el-row>
            </div>
          </el-collapse-item>

        </el-collapse>
        <el-input style="position: relative; bottom:0px;"
                  readonly
                  class="inputNeedCenter"
                  v-model="worldListCurrPage">
          <i slot="prefix"
             style="cursor:pointer;"
             @click="reduceWorldListPage"
             class="el-input__icon el-icon-remove"></i>
          <i slot="suffix"
             class="el-input__icon el-icon-circle-plus"
             @click="addWorldListPage"
             style="cursor:pointer;"></i>
        </el-input>
        <!-- </happy-scroll> -->
        <!-- <el-row style="position: relative; bottom:0px;">
          <el-col :span="8">
            <i class="el-icon-remove" @click="reduceWorldListPage" style="cursor:pointer;"></i>
          </el-col>
          <el-col :span="8">
           {{worldListCurrPage}}
          </el-col>
          <el-col :span="8">
            <i class="el-icon-circle-plus" @click="addWorldListPage" style="cursor:pointer;"></i>
          </el-col>
        </el-row> -->
      </el-aside>
      <el-container>
        <el-header style="height:40px">{{friendName}}</el-header>

        <el-main>
          <div v-for="(item, index) in chatRecord"
               :key="index">
            <div v-show="!item.isMine" style="text-align: left;  margin-top:5px;">
              <el-avatar :size = '32'>{{item.name}}</el-avatar>
              {{item.content}}</div>
            <div v-show="item.isMine" style="text-align: right; margin-top:5px;">
              {{item.content}}<el-avatar :size = '32'>{{item.name}}</el-avatar>
              </div>
              
          </div>
        </el-main>

        <el-footer style="height:40px">
          <el-input v-model="messageContent"
                    @keyup.enter.native="sendMessage"
                    placeholder="请输入内容">
            <i slot="suffix"
               @click="sendMessage"
               style="cursor:pointer;"
               class="el-input__icon el-icon-iconfont-icon-send-line"></i>
          </el-input>
        </el-footer>
      </el-container>
    </el-container>

    <div>

    </div>
    <div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import { Message } from 'element-ui'
// import Avatar from 'vue-avatar'
import login from '../components/login'
import Vue from 'vue'

export default {

  name: 'index',

  components: {
    login
  },
  data () {
    return {

      basepath: "ws://localhost:8080/websocket/",
      path: '',
      socket: "",
      signIn: 'Sign In',
      username: '',

      drawer: false,
      direction: 'rtl',
      messageContent: '',

      currentDate: new Date(),
      dialogWidth: "20%",
      msg: 'Welcome to Quan',
      activeIndex2: '1',
      friendName: '谢新权',
      activeNames: ['1'],
      worldList: [{ 'name': "Huan" }, { 'name': "Ting" }],
      worldListCurrPage: 1,
      friendInfor: {},
      chatRecord: [{ 'name': '新', 'content': 'Muggle.' ,'isMine' : true}, { 'name': '权', 'content': 'Muggle.' ,'isMine' : false}],
    }

  },
  methods: {
    handleSelect (key, keyPath) {
    },
    handleClose (done) {
      done();
    },
    showLoginPage () {
      if (!this.isEmpty(this.username)) return
      this.$store.state.isShowLogin = true
    },
    receiveLoginPageInfor (data) {
      if (this.isEmpty(data)) return
      if (!data.isLogin) return
      this.signIn = ''
      this.username = data.name
      this.path = this.basepath + data.id
      this.init()
      this.loadWorldList()

    },
    isEmpty (data) {
      if (typeof data == 'undefined' || data == null || data == '') return true
    },
    perfectInfor () {

    },
    exit () {
      axios.post('/login/logout').then(response => {

        this.signIn = 'Sign In'
        this.username = ''
        Message.success(response)
      })
    },
    sendMessage () {
      if (this.isEmpty(this.friendInfor.id)) {
        Message.warning("请先选择一位朋友..")
        return
      }
      axios.post('/message/send', {
        'toUserId': this.friendInfor.id,
        'content': this.messageContent
      }).then(response => {
        if (this.isEmpty(response)) return
        this.chatRecord.push({ 'content': this.messageContent })
        this.messageContent = ''

      })
    },
    loadWorldList (pageIndex) {
      pageIndex = this.isEmpty(pageIndex) ? 1 : pageIndex
      axios.post("/user/allUser", {
        "pageIndex": pageIndex, "pageCount": 10
      }).then(response => {
        if (this.isEmpty(response)) return
        var data = response
        var temp = []
        for (var i = 0; i < data.length; i++) {
          temp[i] = { 'name': data[i].name, 'id': data[i].id }
        }
        this.worldList = temp
      })
    },
    init () {
      if (typeof (WebSocket) === "undefined") {
        Message.error("您的浏览器不支持socket")
      } else {
        // 实例化socket
        this.socket = new WebSocket(this.path)
        // 监听socket连接
        this.socket.onopen = this.open
        // 监听socket错误信息
        this.socket.onerror = this.error
        // 监听socket消息
        this.socket.onmessage = this.getMessage

        this.socket.onclose = this.onclose
      }
    },
    open () {
      console.log("socket连接成功")
    },
    error () {
      console.log("连接错误")
    },
    getMessage (msg) {
      Message.success('收到消息:' + msg.data)
      // console.log(msg)
    },
    send () {
      this.socket.send(params)
    },
    close () {
      this.socket.close()
      console.log("socket已经关闭")
    },
    handleChange (val) {
      // console.log(val)
    },
    addFriend () {

    },
    weChat (id, name) {
      this.friendName = name
      this.friendInfor = { "id": id, "name": name }

    },
    reduceWorldListPage () {
      if (this.worldListCurrPage <= 1) return
      this.worldListCurrPage--
      this.loadWorldList(this.worldListCurrPage)
    },
    addWorldListPage () {
      if (this.worldListCurrPage >= 20) return
      this.worldListCurrPage++
      this.loadWorldList(this.worldListCurrPage)
    }
  },
  mounted () {
    // this.init()
    this.loadWorldList()
  },
  destroyed () {
    // 销毁监听
    if (!this.isEmpty(this.socket)) this.socket.onclose = this.close
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-header {
  background-color: #000;
  color: #fff;
  text-align: center;
  /* line-height: 60px; */
}
.el-footer {
  background-color: rgb(255, 255, 255);
  color: #fff;
  text-align: center;
  /* line-height: 60px; */
}
.el-aside {
  background-color: #d3dce6;
  color: #333;
  /* text-align: center; */
  /* line-height: 200px; */
}
.el-main {
  background-color: #02111d;
  color: #333;
  /* text-align: center; */
  /* line-height: 160px; */
}
/* .el-main p{
  text-align: right;
} */
body > .el-container {
  margin-bottom: 40px;
}
/* .el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}
.el-container:nth-child(7) .el-aside {
  line-height: 320px;
} */
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.rightFloat {
  float: right;
}
a {
  cursor: pointer;
}
.el-header {
  background-color: #636366;
  color: rgb(245, 239, 239);
  text-align: center;
  line-height: 40px;
}
.el-footer {
  background-color: #f0f0f0;
  color: rgb(245, 239, 239);
  text-align: center;
  line-height: 40px;
}
.el-aside {
  background-color: #eff1f3;
  color: #333;
  text-align: center;
}
.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
}
/* body > .el-container {
  margin-bottom: 40px;
} */
/* .el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}
.el-container:nth-child(7) .el-aside {
  line-height: 320px;
} */
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.inputNeedCenter >>> input {
  text-align: center;
}
</style>



