//request.js
import axios from "axios";
import qs from "qs";
import app from "../main.js";
import { Message } from 'element-ui'
import store from '../vuex/store'

// axios.defaults.baseURL = "http://118.89.133.157:8080/"
axios.defaults.baseURL = "http://localhost:8080/"

axios.defaults.withCredentials = true

axios.interceptors.request.use(config => {
    config.method === 'post'
        ? config.data = qs.stringify({...config.data})
        : config.params = {...config.params};
    config.headers['Content-Type'] = 'application/x-www-form-urlencoded';

    return config;
  }, error => {
    Promise.reject(error)
});


/****** respone拦截器==>对响应做处理 ******/
axios.interceptors.response.use(
  response => {  //成功请求到数据
    if (response.data.status == 1) return response.data.data
    //status == 5 Go To Login
    if(response.data.status == 5) {
      Message.warning(response.data.msg + ", 即将打开登录页面..")
      

      setTimeout(()=>{store.state.isShowLogin = true}, 1000);
      return
    }
    Message.error(response.data.msg)
  },
  error => {  //响应错误处理
      Message.error('error');
      Message.error(error);
      Message.error(JSON.stringify(error));

      let text = JSON.parse(JSON.stringify(error)).response.status === 404
          ? '404'
          : '网络异常，请重试';

      return Promise.reject(error)
  }
);


export default axios;


